@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: 'Poppins', sans-serif;
}
.form {
    display: grid;
    margin-top: 10%;
    justify-content: center;
}

input {
    border: none;
    background: hsl(0 0% 93%);
    border-radius: .25rem;
    padding: .75rem 1rem;
}

.btn{
    margin-top: 20px;
}
  
.submit{
    background: hotpink;
    color: white;
    font-size: 15px;
    box-shadow: 0 .75rem .5rem -.5rem hsl(0 50% 80%);
    margin-left: 20px;
}

.clear{
    background: lightblue;
    color: white;
    font-size: 15px;
    border: black;
    box-shadow: 0 .75rem .5rem -.5rem hsl(0 50% 80%);
}

.register{
    font-weight: 300;
    font-style: italic;
}
.user{
    font-weight: 500;
}
.register{
    font-weight: 300;
}

.verify{
    background: hotpink;
    color: white;
    font-size: 15px;
    box-shadow: 0 .75rem .5rem -.5rem hsl(0 50% 80%);
    margin-top: 20px;
}

.otpNotification{
    color:red;
    font-size: 12px;
    margin-top: 3px;
}
.show-container{
    margin-top: 30px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.show{
    background-color:white;
    position: relative;
    border-radius: 3px;
    box-shadow: 3px 3px 5px rgba(0,0,0,0.1);
    overflow: hidden;
    margin: 1rem;
    width: 450px;
    display: flex;
}

.show img{
    object-fit: cover;
    border-radius: 5px;
    height: 250px;
    width: 40%;
}

.show-info{
    padding: 1rem;
    margin-left: 10px;
}

.show-info h3{
    color: #4a4e69;
    font-weight: 600;
    margin: 0;
}


.rating{
    background-color: white;
    border-radius: 5px;
    font-weight: bold;
    padding: 0.5rem;
}

.rating.green{
    color: #66CD00;
    font-weight: 700;
}

.rating.orange{
    color: orange;
}

.rating.red{
    color: red;
}

.show-location{
    margin-top: 10px;
    color: #adb5bd;
    font-weight: 600;
    font-size: 14px;
}

.show-cuisine{
    margin-top: 10px;
    color: #adb5bd;
    font-weight: 600;
    font-size: 14px;
}

.discount{
    margin-top: 10px;
    color: #f4a261;
}

.bottom-section{
    display: flex;
    margin-top: 25px;
}

.star{
    background:black;
    -webkit-clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 83% 100%, 50% 70%, 18% 100%, 32% 57%, 2% 35%, 39% 35%);
            clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 83% 100%, 50% 70%, 18% 100%, 32% 57%, 2% 35%, 39% 35%);
}

.popularity{
    color: #adb5bd;
    font-weight: 400;
    font-size: 12px;
}

.second-part{
    margin-left: 80px;
}
